import { Routes, RouterModule } from "@angular/router";
import { COURSE_ROUTES } from "app/courses/course.routes";
import { STUDENT_ROUTES } from "app/student/student-routing.module";
import { ENQUIRY_ROUTES } from "app/enquiry/enquiry.routes";

//Route for content layout with sidebar, navbar and footer
export const Full_ROUTES: Routes = [
  {
    path: "changelog",
    loadChildren: () =>
      import("../../changelog/changelog.module").then(m => m.ChangeLogModule)
  },
  {
    path: "full-layout",
    loadChildren: () =>
      import("../../pages/full-layout-page/full-pages.module").then(
        m => m.FullPagesModule
      )
  },
  {
    path: "users",
    loadChildren: () =>
      import("../../users/users.module").then(m => m.UsersModule)
  },
  {
    path: "teacher",
    loadChildren: () =>
      import("../../teacher/teacher.module").then(m => m.TeacherModule)
  },
  {
    path: "exam",
    loadChildren: () => import("../../exam/exam.module").then(m => m.ExamModule)
  },
  {
    path: "course",
    children: COURSE_ROUTES
  },
  {
    path: "student",
    children: STUDENT_ROUTES
  },
  {
    path: "enquiry",
    children: ENQUIRY_ROUTES
  },
  {
    path: "email",
    loadChildren: () =>
      import("../../email/email.module").then(m => m.EmailModule)
  },
  {
    path: "sms",
    loadChildren: () => import("../../sms/sms.module").then(m => m.SmsModule)
  },
  {
    path: "hr",
    loadChildren: () => import("../../hr/hr.module").then(m => m.HrModule)
  },
  {
    path: "account",
    loadChildren: () =>
      import("../../account/account.module").then(m => m.AccountModule)
  }
];
